<template>
  <div class="userResource">
    <div class="row">
      <div class="col-lg-3 col-md-3 col-sm-0 col-xs-0 Mohide">
        <div class="leftMenus">
          <div class="navTile">
            教学资源
          </div>
          <el-menu
            :default-active="typeId"
            class="el-menu-vertical-demo">
            <router-link :to="{name:'Videos'}">
              <el-menu-item index="Videos">
                <i class="el-icon-arrow-right"></i>
                <span slot="title">教学视频</span>
              </el-menu-item>
            </router-link>
            <router-link :to="{name:'Files'}">
              <el-menu-item index="Files">
                <i class="el-icon-arrow-right"></i>
                <span slot="title">教学课件</span>
              </el-menu-item>
            </router-link>
          </el-menu>
        </div>
      </div>
      <div class="col-lg-9 col-md-9 col-sm-12 col-xs-12">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  export default {
    computed:{
      //子导航目录选中ID
      ...mapState(['typeId'])
    }
  }
</script>

<style lang="less">
  .userResource{
    padding-top: 30px;
    padding-bottom: 20px;
    .leftMenus{
      padding-left: 20px;
      .navTile{
        height: 45px;
        color: #fff;
        font-size: 16px;
        line-height: 45px;
        font-weight: bold;
        overflow: hidden;
        border-radius: 5px 5px 0 0;
        background: rgb(85,171,235);
        text-align: center;
      }
      .el-menu{
        border: solid 1px #e6e6e6;
        border-bottom: solid 0px #e6e6e6;
        border-radius: 0 0 5px 5px;
        a{
          text-decoration: none;
        }
        li{
          height: 50px;
          line-height: 50px;
          border-bottom: solid 1px #e6e6e6;
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      padding-top: 20px;
      padding-bottom: 0px;
      .Mohide{
        display: none;
      }
    }
  }
</style>



